<template>
<div class="flex">
  <div class="UserInfo">
    <h3>Данные игрока</h3>
    Логин: {{User.login}}<br/>
    Почта: {{User.email}}<br/>
  </div>
  <div class="GamesInfo">
    <h1>Сохраненные игры</h1>
    <table border="1">
      <thead>
        <td>Номер</td>
        <td>Название</td>
        <td>Описание</td>
        <td>Дата создания</td>
        <td>Дата изменения</td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
      </thead>
      <tr v-for="(games, game_i) in Games" :key="game_i">
        <td>{{games._id}}</td>
        <td>{{games.gameState[2]}}</td>
        <td>{{games.gameState[3]}}</td>
        <td>{{games.createdAt}}</td>
        <td>{{games.updatedAt}}</td>
        <td><input type="button" value="Загрузить"></td>
        <td><input type="button" value="Удалить"></td>
        <td><input type="button" value="Изменить"></td>
        <input type="button" value="Доп. инфо">
      </tr>
    </table>
  </div>
</div>
</template>

<script>
import { mapActions, mapGetters} from 'vuex'
import axios from "axios";

export default {
  data(){
      return{
        User:{},
        Games:{},
      }
  },
  methods:{
    ...mapActions('auth',['login']),
    ...mapActions('auth',['logout']),
    ...mapGetters('auth',['isAuthenticated']),
  },
  async beforeMount()
  {
    let response = await axios("https://optimizators.herokuapp.com/user/", {
      method: "post",
    });

    this.User = response.data.user;
    this.Games = response.data.games;
    console.log(response)
    
  }
}
</script>
<style scoped>
.flex{
  display:flex;
  width:90vh;
}
.UserInfo{
  width:40%
}
.GamesInfo{
  width:60%
}
</style>